/* decoration -------------------------------------------------- */
.c-decoration {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  padding: 1rem;
  font-size: 0.5rem;
  line-height: 1;
  opacity: 0.5;
}

.c-decoration.-reverse {
  right: 0;
  left: auto;
}

@media screen and (min-width: 700px) {
  .c-decoration {
    padding: 1.5rem;
  }
}

.c-decoration_text {
  position: relative;
  padding-bottom: 5em;
  writing-mode: vertical-lr;
}

.c-decoration_text::after {
  position: absolute;
  bottom: 0;
  left: 0.5em;
  display: block;
  width: 0.0625rem;
  height: 4em;
  content: '';
  background-color: #000;
}

/* icon css -------------------------------------------------- */
.c-icon {
  display: inline-block;
  width: 1.1em;
  height: 1.1em;
  vertical-align: middle;
  fill: currentColor;
}

.c-hamburger {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 50%;
  height: 50%;
  margin: auto;
  vertical-align: middle;
}

.c-hamburger_line {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.125rem;
  margin: auto;
  background-color: transparent;
  border-radius: 0;
  transition: 150ms;
  transform-origin: left;
}

.c-hamburger_line::before, .c-hamburger_line::after {
  position: absolute;
  display: block;
  height: 100%;
  content: '';
  background-color: currentColor;
  border-radius: 0;
}

.c-hamburger_line::before {
  top: 0;
  width: 100%;
  margin-top: -16%;
}

.c-hamburger_line::after {
  bottom: 0;
  width: 64%;
  margin-bottom: -16%;
}

.btn:hover .c-hamburger_line {
  transform: scaleX(0.6);
}

.c-arrow {
  display: block;
  width: 100%;
  height: 100%;
  border-top: 0.1875rem solid currentColor;
  border-right: 0.1875rem solid currentColor;
  transform: rotate(45deg);
}

.c-arrow.-left {
  transform: rotate(225deg);
}

.c-arrow.-top {
  transform: rotate(-45deg);
}

.c-arrow.-bottom {
  transform: rotate(135deg);
}

.c-arrowRound {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  border-top: 0.5rem solid currentColor;
  border-right: 0.5rem solid currentColor;
  border-top-right-radius: 0.25rem;
  transform: rotate(45deg);
}

.c-arrowRound::before, .c-arrowRound::after {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  content: '';
  background-color: currentColor;
  border-radius: 6.25rem;
}

.c-arrowRound::before {
  top: -0.5rem;
  left: -0.25rem;
}

.c-arrowRound::after {
  right: -0.5rem;
  bottom: -0.25rem;
}

.c-arrowRound.-left {
  transform: rotate(225deg);
}

.c-arrowRound.-top {
  transform: rotate(-45deg);
}

.c-arrowRound.-bottom {
  transform: rotate(135deg);
}

