/* button common */
.btn {
  font-style: normal;
  font-weight: 700;
  line-height: 1.8;
  text-align: center;
  text-indent: 0.1em;
  letter-spacing: 0.1em;
  font-feature-settings: 'palt' 1;
  font-family: 'Noto Sans JP', sans-serif;
  display: block;
  margin-right: auto;
  margin-left: auto;
  font-size: 1.2rem;
  cursor: pointer;
}

.btn_text::before {
  display: block;
  width: 0;
  height: 0;
  margin-top: calc((1 - 1.8) * 0.5em);
  content: '';
}

.btn_text::after {
  display: block;
  width: 0;
  height: 0;
  margin-bottom: calc((1 - 1.8) * 0.5em);
  content: '';
}

.btn_link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.btn_icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.2em;
  fill: currentColor;
}

.btn_icon span {
  font-size: 1.2em;
}

.btnBox {
  display: block;
  width: 100%;
  max-width: 25rem;
  margin-top: 1.5rem;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  transition: 200ms;
  transition-timing-function: ease;
  transform: translateY(0);
}

.btnBox:hover {
  transform: translateY(-3%);
}

@media screen and (min-width: 700px) {
  .btnBox {
    margin-top: 2rem;
  }
}

.btnBox.-hero {
  max-width: 50rem;
  margin-top: 1.5rem;
  margin-left: 0;
}

@media screen and (min-width: 700px) {
  .btnBox.-hero {
    margin-top: 2.5rem;
  }
}

@media screen and (min-width: 900px) {
  .btnBox.-hero {
    margin-top: 0.5rem;
  }
}

/* button primary */
.btn.-primary {
  background-image: linear-gradient(45deg, #27c488 0%, #50b9e8 100%);
  position: relative;
  width: 100%;
  color: #fff;
  text-align: center;
  transition: 200ms;
  transition-timing-function: ease;
  transform: translateY(0);
}

.btn.-primary::before {
  background-image: linear-gradient(45deg, #00b36e 0%, #00b2ab 100%);
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  content: '';
  opacity: 1;
  transition: 200ms;
  transition-timing-function: ease;
}

.btnBox:hover .btn.-primary {
  box-shadow: 0 0.625rem 1.875rem rgba(71, 163, 204, 0.5);
}

.btnBox:hover .btn.-primary::before {
  opacity: 0;
}

.btn.-primary .btn_link {
  position: relative;
  padding: 1.2em 1.6em;
}

