/* form -------------------------------------------------- */
/* form リセット  */
input,
button,
textarea,
select {
  color: inherit;
  border-radius: 0;
  appearance: none;
}

fieldset {
  display: block;
  padding: 0;
  border: none;
}

legend {
  display: block;
  padding: 0;
  margin: 0;
  border: 0;
}

label,
button {
  cursor: pointer;
}

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button,
input[type='month']::-webkit-outer-spin-button,
input[type='month']::-webkit-inner-spin-button,
input[type='datetime-local']::-webkit-outer-spin-button,
input[type='datetime-local']::-webkit-inner-spin-button,
input[type='week']::-webkit-outer-spin-button,
input[type='week']::-webkit-inner-spin-button,
input[type='time']::-webkit-outer-spin-button,
input[type='time']::-webkit-inner-spin-button,
input[type='date']::-webkit-outer-spin-button,
input[type='date']::-webkit-inner-spin-button {
  /* -webkit-appearance: none;
	margin: 0; */
  display: none;
}

input[type='number'],
input[type='month'],
input[type='datetime-local'],
input[type='week'],
input[type='time'],
input[type='date'] {
  -moz-appearance: textfield;
}

input[type='date']::-webkit-clear-button,
input[type='month']::-webkit-clear-button,
input[type='datetime-local']::-webkit-clear-button,
input[type='time']::-webkit-clear-button,
input[type='week']::-webkit-clear-button {
  -webkit-appearance: none;
}

/* form デザイン  */
.form input::placeholder,
.form textarea::placeholder {
  color: rgba(26, 26, 26, 0.3);
}

.form input:-ms-input-placeholder,
.form textarea:-ms-input-placeholder {
  color: rgba(26, 26, 26, 0.3);
}

.form input::-ms-input-placeholder,
.form textarea::-ms-input-placeholder {
  color: rgba(26, 26, 26, 0.3);
}

.form input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px #eee inset;
  -webkit-text-fill-color: #333 !important;
}

.form textarea {
  width: 100%;
  padding: 0.8em;
  resize: none;
  background-color: white;
  border: 0.125rem solid #e0e0e0;
  border-radius: 0.25rem;
}

.form textarea:focus {
  border: 0.125rem solid #f09d89;
  outline: none;
  box-shadow: inset 0 0 0 0.0625rem #f09d89;
}

.form input {
  width: 100%;
  padding: 0.8em;
  background-color: white;
  border: 0.125rem solid #e0e0e0;
  border-radius: 0.25rem;
  outline: 0;
  transition: border 0.2s ease-out;
}

.form input:focus {
  border: 0.125rem solid #f09d89;
  outline: none;
  box-shadow: inset 0 0 0 0.0625rem #f09d89;
}

.form input[type='submit'] {
  padding: 1.2em;
  font-size: 1.2em;
  color: white;
  background-color: #1a1a1a;
  border: none;
  transition: 150ms;
}

.form input[type='submit']:hover {
  background-color: #4887ff;
  transform: translateY(-0.3em);
}

.form input[type='radio'] {
  position: relative;
  top: 0;
  left: 0;
  z-index: 0;
  display: inline-block;
  width: 1.7em;
  height: 1.7em;
  padding: 0;
  overflow: hidden;
  vertical-align: middle;
  background-color: transparent;
  border: 0;
  border-radius: 50%;
}

.form input[type='radio']::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 100%;
  content: '';
  background-color: white;
  border: 0.125rem solid #e0e0e0;
  border-radius: 50%;
}

.form input[type='radio']::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: block;
  width: 48%;
  height: 48%;
  margin: auto;
  content: '';
  background-color: #4887ff;
  border-radius: 50%;
  opacity: 0;
  transition: 150ms;
  transform: scale(0.2);
}

.form input[type='radio']:checked::after {
  opacity: 1;
  transform: scale(1);
}

.form input[type='radio']:disabled {
  opacity: 0.6;
}

.form input[type='radio']:focus {
  outline: none;
  box-shadow: 0 0 0 0.1875rem #f09d89;
}

.form input[type='checkbox'] {
  position: relative;
  top: 0;
  left: 0;
  z-index: 0;
  display: inline-block;
  width: 1.7em;
  height: 1.7em;
  padding: 0;
  overflow: hidden;
  vertical-align: middle;
  background-color: transparent;
  border: 0;
  border-radius: 0;
}

.form input[type='checkbox']::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 100%;
  content: '';
  background-color: white;
  border: 0.125rem solid #e0e0e0;
  border-radius: 0;
}

.form input[type='checkbox']::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: block;
  width: 48%;
  height: 64%;
  margin: auto auto 28% auto;
  content: '';
  border-right: 0.3125rem solid #4887ff;
  border-bottom: 0.3125rem solid #4887ff;
  opacity: 0;
  transition: 150ms;
  transform: rotate(45deg) scale(0.8);
}

.form input[type='checkbox']:checked::after {
  opacity: 1;
  transform: rotate(45deg) scale(1);
}

.form input[type='checkbox']:disabled {
  opacity: 0.6;
}

.form input[type='checkbox']:focus {
  outline: none;
  box-shadow: 0 0 0 0.1875rem #f09d89;
}

.form input[type='checkbox']:focus::after {
  opacity: 1;
  transform: rotate(45deg) scale(1);
}

.form select {
  width: 12.5rem;
  padding: 0.8em;
  padding-right: 3em;
  background-color: white;
  border: 0.125rem solid #bdbdbd;
  border-radius: 0.25rem;
}

.form select:focus {
  border: 0.125rem solid #f09d89;
  outline: none;
  box-shadow: inset 0 0 0 0.0625rem #f09d89;
}

.form .selectBox {
  position: relative;
  display: inline-block;
}

.form .selectBox::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: block;
  width: 0;
  height: 0;
  margin: auto 1em auto auto;
  pointer-events: none;
  content: '';
  border-color: #bdbdbd transparent transparent transparent;
  border-style: solid;
  border-width: 0.6em 0.4em 0 0.4em;
}

.form button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  transition: 150ms;
}

.form button:hover, .form button:focus {
  background-color: #4887ff;
  transform: scale(1.1);
}

.form button:active {
  background-color: #4887ff;
  transform: scale(1.1);
}

.form input:disabled {
  opacity: 0.5;
}

/* form レイアウト */
input[type='radio'] + label,
input[type='checkbox'] + label,
label + input[type='radio'],
label + input[type='checkbox'] {
  margin-left: 0.3em;
}

.form {
  width: 100%;
}

.form .box + .box {
  margin-top: 1.5em;
}

.form .box.-indent {
  padding-top: 1em;
  padding-bottom: 1em;
  padding-left: 2em;
  margin-left: 2em;
  border-left: 0.125rem solid #e0e0e0;
}

.form .wrap + .wrap {
  margin-top: 1em;
}

.form .title {
  margin-bottom: 0.8em;
}

.form .item + .item {
  margin-top: 0.8em;
}

.form .closed {
  height: auto;
  overflow: hidden;
  opacity: 1;
  transition: opacity 500ms;
}

.form .closed:disabled {
  height: 0;
  padding: 0;
  margin: 0;
  pointer-events: none;
  opacity: 0;
}

/* form コンポーネント */
