/* heading -------------------------------------------------- */
/* h1 */
.heading1 {
  color: #4887ff;
}

.heading1::before {
  display: block;
  width: 0;
  height: 0;
  margin-top: calc((1 - 1.8) * 0.5em);
  content: '';
}

.heading1::after {
  display: block;
  width: 0;
  height: 0;
  margin-bottom: calc((1 - 1.8) * 0.5em);
  content: '';
}

@media screen and (min-width: 900px) {
  .heading1 {
    margin-right: -24%;
  }
}

.heading1_underLine {
  padding-bottom: 0.2em;
  border-bottom: 0.1875rem solid #4887ff;
}

/* h2 */
.heading2 {
  position: relative;
  padding-bottom: 1.8125rem;
