/* button -------------------------------------------------- */

/* button common */
.btn {
	@include fs-btn();
	@include ff-jp();

	display: block;
	margin-right: auto;
	margin-left: auto;
	font-size: 1.2rem; //親でサイズを決めると、textのlineHeightCropが想像通りに機能する
	cursor: pointer;
}

.btn_text {
	@include lineHeightCrop(1.8);
}

//リンクの中は中心揃え
.btn_link {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
}

//アイコンの位置
.btn_icon {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	margin-left: 0.2em; //
	fill: currentColor;

	//アイコンフォント
	span {
		font-size: 1.2em;
	}
}

//ボックス
.btnBox {
	display: block;
	// position: relative;
	width: 100%;
	max-width: rem(400); //これがボタンのサイズ
	margin-top: rem(24);
	margin-right: auto;
	margin-left: auto;
	text-align: center;
	transition: 200ms;
	transition-timing-function: ease;
	transform: translateY(0);

	&:hover {
		transform: translateY(-3%);
	}

	@include mq() {
		margin-top: rem(32);
	}
}

//horoのボックス
.btnBox.-hero {
	max-width: rem(800); //フルサイズ
	margin-top: rem(24);
	margin-left: 0;

	@include mq() {
		margin-top: rem(40);
	}

	@include mq(lg) {
		margin-top: rem(8);
	}
}

//--------------------------------------------------

/* button primary */

//ホバー時のボタンデザイン
.btn.-primary {
	@include gr-key-change();

	position: relative;
	width: 100%; //基本フルサイズのボタンサイズはラップで
	color: #fff;
	text-align: center;
	transition: 200ms;
	transition-timing-function: ease; //特にこだわらないならeaseが一番気持ち良い
	transform: translateY(0);

	//通常のボタンデザイン(ホバーで透明にできる)
	&::before {
		@include gr-key();

		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		opacity: 1;
		transition: 200ms;
		transition-timing-function: ease;
	}

	//ホバー
	.btnBox:hover & {
		@include shadow-key($size: rem(30), $opacity: 0.5);

		//通常のボタンデザイン
		&::before {
			opacity: 0;
		}
	}

	//a
	.btn_link {
		position: relative; //zindex
		padding: 1.2em 1.6em;
	}
}

