/* section -------------------------------------------------- */

//ollist
.feature {
	counter-reset: featureItemCount; //どの要素でリセットするか。[カウンタ名,0に値する数字]

	//li
	&_item {
		position: relative;
		counter-increment: featureItemCount; //カウントする要素 [カウンタ名,１単位]

		//数字の部分 contentでcounterの値を使用,スタイルを設定可能 decimal が初期
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			display: inline-block;
			width: 10%;
			height: 10%;
			color: $cl-point;
			content: counter(featureItemCount, decimal-leading-zero);
		}

		//２の倍数で互い違いに
		&:nth-of-type(2n) {
			margin-left: auto;
		}

		@include mq() {
			width: 80%;
		}
	}
}

/* section -------------------------------------------------- */

.employeeIntro {
	position: relative;
	display: flex;
	flex-flow: column-reverse nowrap;
	width: 100%; //ie

	.info {
		@include mq() {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;

			&:hover {
				opacity: 1;
			}
		}
	}

	.media {
	}
}

/* PRODUCT -------------------------------------------------- */

.product_img {
	background-color: rgba($color: #000, $alpha: 0.2);
}

//インナーがわり
.productThumbs {
	width: 84%; //375pxに対し左右に30px
	max-width: rem(600);
	margin-top: rem(16);
	margin-right: auto;
	margin-left: auto;

	@include mq() {
		margin-top: rem(24);
	}
}

//要素
.productThumbs {
	&_title {
		@include lineHeightCrop(1.8);

		position: relative;
		padding-bottom: rem(16 + 2); //余白+装飾ラインの太さ
		color: #fff;

		//見出し下のライン
		&::before {
			position: absolute;
			bottom: 0;
			left: 50%;
			display: block;
			width: rem(40);
			height: rem(2);
			margin-left: rem(-20); //半分
			content: '';
			background-color: currentColor;
		}
	}

	&_text {
		@include lineHeightCrop(1.8);

		margin-top: rem(16);
		font-size: 0.6rem;
		font-weight: 400;
		color: #fff;
	}
}

