/* form -------------------------------------------------- */

/* form リセット  */

input,
button,
textarea,
select {
	// 文字色を親から継承
	color: inherit;

	// iOSの角丸をリセット
	border-radius: 0;

	// デフォルトスタイルをリセット
	// -webkit-appearance: none;
	// -moz-appearance: none;
	appearance: none;
}

fieldset {
	display: block;
	padding: 0;
	border: none;
}

legend {
	display: block;
	padding: 0;
	margin: 0;
	border: 0;
}

label,
button {
	//iOSでのlabelとinput,select,textareaの関連付け
	cursor: pointer;
}

//スピンボタン非表示 chrome,safari
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button,
input[type='month']::-webkit-outer-spin-button,
input[type='month']::-webkit-inner-spin-button,
input[type='datetime-local']::-webkit-outer-spin-button,
input[type='datetime-local']::-webkit-inner-spin-button,
input[type='week']::-webkit-outer-spin-button,
input[type='week']::-webkit-inner-spin-button,
input[type='time']::-webkit-outer-spin-button,
input[type='time']::-webkit-inner-spin-button,
input[type='date']::-webkit-outer-spin-button,
input[type='date']::-webkit-inner-spin-button {
	/* -webkit-appearance: none;
	margin: 0; */
	display: none;
}

//スピンボタン非表示(firefox)
input[type='number'],
input[type='month'],
input[type='datetime-local'],
input[type='week'],
input[type='time'],
input[type='date'] {
	-moz-appearance: textfield;
}

//クリアボタン非表示
input[type='date']::-webkit-clear-button,
input[type='month']::-webkit-clear-button,
input[type='datetime-local']::-webkit-clear-button,
input[type='time']::-webkit-clear-button,
input[type='week']::-webkit-clear-button {
	-webkit-appearance: none;
}

//-------------------------------------------------------------------------------------------

//mixin

@mixin formItemFucus() {
	// filter: drop-shadow(0 rem(4) rem(8) transparentize($cl-required, 0.3));
	border: rem(2) solid $cl-required;
	outline: none;
	box-shadow: inset 0 0 0 rem(1) $cl-required; //whatinput上書き・線を内側に太くする
}

@mixin formItemFucus_radio() {
	// filter: drop-shadow(0 0 rem(3) $cl-required);
	outline: none;
	box-shadow: 0 0 0 rem(3) $cl-required; //whatinput上書き・線は外側に。
}

