	//-------------------------------------------------------------------------------------------

	// //radio

	//opacityを使うとforcus時のデザインができなくなる
	//ラジオボタンは透明・フォーカスに使用
	input[type='radio'] {
		position: relative;
		top: 0;
		left: 0;
		z-index: 0;
		display: inline-block;
		width: 1.7em;
		height: 1.7em;
		padding: 0; //リセット
		overflow: hidden;
		vertical-align: middle;
		background-color: transparent;
		border: 0;
		border-radius: 50%; //フォーカスのために同じ形に

		//box
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			display: block;
			width: 100%;
			height: 100%;
			content: '';
			background-color: $g100;
			border: rem(2) solid $g60;
			border-radius: 50%;
		}

		//checkmark
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 2;
			display: block;
			width: 48%;
			height: 48%;
			margin: auto;
			content: '';
			background-color: $cl-key;
			border-radius: 50%;
			opacity: 0;
			transition: 150ms;
			transform: scale(0.2);
		}

		//checked
		&:checked {
			&::after {
				opacity: 1;
				transform: scale(1);
			}
		}

		//disabled
		&:disabled {
			opacity: 0.6;
		}

		//フォーカス
		&:focus {
			@include formItemFucus_radio();
		}
	}

	//-------------------------------------------------------------------------------------------

	//checkbox

	//元々のチェックボックスは透明に・フォーカスに使用
	input[type='checkbox'] {
		position: relative;
		top: 0;
		left: 0;
		z-index: 0;
		display: inline-block;
		width: 1.7em;
		height: 1.7em;
		padding: 0; //リセット
		overflow: hidden;
		vertical-align: middle;
		// opacity: 0;//opacityを使うとforcus時のデザインができなくなる
		background-color: transparent;
		border: 0;
		border-radius: 0; //フォーカスのために同じ形に

		//box
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			display: block;
			width: 100%;
			height: 100%;
			content: '';
			background-color: $g100;
			border: rem(2) solid $g60;
			border-radius: 0; //フォーカスのために同じ形に
		}

		//checkmark
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 2;
			display: block;
			width: 48%;
			height: 64%; //長い方の辺
			margin: auto auto 28% auto;
			content: '';
			border-right: rem(5) solid $cl-key;
			border-bottom: rem(5) solid $cl-key;
			opacity: 0;
			transition: 150ms;
			transform: rotate(45deg) scale(0.8);
		}

		//checked
		&:checked {
			&::after {
				opacity: 1;
				transform: rotate(45deg) scale(1);
			}
		}

		//disabled
		&:disabled {
			opacity: 0.6;
		}

		//フォーカス
		&:focus {
			@include formItemFucus_radio();

			&::after {
				opacity: 1;
				transform: rotate(45deg) scale(1);
			}
		}
	}

