	select {
		width: rem(200); //大きさ固定値に
		padding: 0.8em;
		padding-right: 3em; //三角形アイコンのスペース
		background-color: $g100;
		border: rem(2) solid $g50;
		border-radius: rem(4);

		//フォーカス
		&:focus {
			@include formItemFucus();
		}
	}

	//セレクトのwrap
	.selectBox {
		position: relative;
		display: inline-block;

		//三角形アイコン
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 2;
			display: block;
			width: 0;
			height: 0;
			margin: auto 1em auto auto;
			pointer-events: none;
			content: '';
			border-color: $g50 transparent transparent transparent;
			border-style: solid;
			border-width: 0.6em 0.4em 0 0.4em; //各三角形の長さ 下三角形部分だけ0にして無くす
		}
	}

