//-------------------------------------------------------------------------------------------

/* form レイアウト */

//隣り合ったラジオorチェックとラベルにはマージン
input[type='radio'] + label,
input[type='checkbox'] + label,
label + input[type='radio'],
label + input[type='checkbox'] {
	margin-left: 0.3em;
}

.form {
	width: 100%;

	.box {
		& + .box {
			margin-top: 1.5em;
		}

		&.-indent {
			padding-top: 1em;
			padding-bottom: 1em;
			padding-left: 2em;
			margin-left: 2em;
			border-left: rem(2) solid $g60;
		}
	}

	.wrap {
		& + .wrap {
			margin-top: 1em;
		}
	}

	.title {
		margin-bottom: 0.8em;
	}

	.item {
		& + .item {
			margin-top: 0.8em;
		}
	}

	.closed {
		height: auto;
		overflow: hidden;
		opacity: 1;
		transition: opacity 500ms;

		&:disabled {
			height: 0;
			padding: 0;
			margin: 0;
			pointer-events: none;
			opacity: 0;
		}
	}
}

//-------------------------------------------------------------------------------------------

/* form コンポーネント */

/* 必須・任意項目 */
.cl-requiredItem {
	display: inline-block;
	padding: 0.5em 0.8em;
	margin-right: 1em;
	font-size: 0.8em;
	color: $g100;
	border-radius: 0.3em;

	&.-cl-required {
		background-color: $cl-required;
	}
	&.-optional {
		background-color: $g50;
	}
}

/* 注文個数カウント */
.orderCount {
	display: inline-flex;
	align-items: center;

	&_button {
		flex: none;
		width: 2em;
		height: 2em;
		margin-right: 0.2em;
		margin-left: 0.2em;
		color: $g100;
		background-color: $g50;
		border-radius: 20%;

		&:hover {
			background-color: $cl-black;
		}

		&:active {
			transform: scale(1.1);
		}
	}

	&_input {
		flex: none;
		width: rem(48);
		padding: 0.5em;
		margin-right: 0.2em;
		margin-left: 0.2em;
		background-color: $g100;
		border: rem(2) solid $g60;

		@include mq() {
			width: rem(80);
		}
	}
}
